<template>
  <div class="preview-view">
    <div
      class="screen-content"
      :style="`width:${screenWidth}px;height:${screenHeight}px;
        transform:translate(${getMinValue(defaultLeft)}px, ${getMinValue(
        defaultTop
      )}px) scale(${scaleSize / 100});transform-origin:0 0;`"
    >
      <img
        :src="require(`@/designer/assets/images/map${curMapId}.png`)"
        alt=""
      />
      <div class="screen-decoration">
        <!-- 装饰图层 -->
        <!-- <img src="@/designer/assets/images/map2.png" alt="" /> -->
        <img
          v-for="(imgItem, index) in decorationImgList"
          :key="index"
          class="position-img"
          :class="imgItem.position"
          :src="require(`@/designer/assets/images/${column}/${imgItem.bg}.png`)"
        />
        <div class="position-title">{{ screenTitle }}</div>
        <div class="grid-stack" :class="`grid-stack-style${column}`">
          <div
            class="grid-stack-item"
            v-for="item in nodesArr"
            :gs-id="item.id"
            :id="item.id"
            :key="item.id"
            :gs-w="item.w"
            :gs-h="item.h"
            :gs-x="item.x"
            :gs-y="item.y"
          >
            <div class="grid-stack-item-content">
              <component
                :is="item.componentName"
                v-bind="item.props"
                :node="item"
              />
            </div>
          </div>
        </div>
      </div>
      <img
        class="full-screen-icon"
        src="@/designer/assets/images/full-screen.png"
        alt=""
        @click="handleFullScreen"
      />
    </div>
  </div>
</template>



<script >
import 'gridstack/dist/gridstack.min.css';
import '@/styles/gridstack-column.scss';
import { GridStack } from 'gridstack';
import { SCREEN_SIZE } from '@/designer/constants/index';
import { computed } from '@vue/composition-api';
import View1 from '@/designer/assets/components/view1/index.vue';
import View2 from '@/designer/assets/components/view2/index.vue';
import Condition from '@/designer/assets/components/condition/index.vue';
import MapDocument from '@/designer/assets/components/map-document/index.vue';
const __sfc_main = {
  name: 'PreviewRender',
  components: {
    View1,
    View2,
    Condition,
    MapDocument
  }
};
__sfc_main.props = {
  nodes: {
    type: Array,
    default: () => []
  },
  map: {
    type: Object,
    default: () => ({})
  },
  screen: {
    type: Object,
    default: () => ({})
  }
};
__sfc_main.setup = (__props, __ctx) => {
  const props = __props;
  const curMapId = computed(() => {
    return props.map.props.mapId;
  });
  const column = computed(() => {
    return props.map.props.column;
  });
  const screenTitle = computed(() => {
    return props.map.props.title;
  });
  const decorationImgList = [{
    bg: 'top-bg',
    position: 'position-top'
  }, {
    bg: 'right-bg',
    position: 'position-left'
  }, {
    bg: 'right-bg',
    position: 'position-right'
  }, {
    bg: 'bottom-bg',
    position: 'position-bottom'
  }];
  const screenWidth = ref(SCREEN_SIZE[column.value].width);
  const screenHeight = ref(SCREEN_SIZE[column.value].height);
  const windowInnerWidth = ref(window.innerWidth);
  const windowInnerHeight = ref(window.innerHeight);
  const scaleWidth = computed(() => {
    return windowInnerWidth.value / screenWidth.value * 100;
  }); // 默认缩小
  const scaleHeight = computed(() => {
    return windowInnerHeight.value / screenHeight.value * 100;
  });
  const scaleSize = computed(() => {
    return scaleWidth.value < scaleHeight.value ? scaleWidth.value : scaleHeight.value;
  });
  const defaultTop = computed(() => {
    return (windowInnerHeight.value - windowInnerWidth.value / screenWidth.value * screenHeight.value) / 2;
  }); // 垂直居中

  const defaultLeft = computed(() => {
    return (windowInnerWidth.value - windowInnerHeight.value / screenHeight.value * screenWidth.value) / 2;
  }); // 水平居中

  const getMinValue = value => {
    return value < 0 ? 0 : value;
  };
  const toggleFullScreen = () => {
    if (!document.fullscreenElement) {
      // 进入全屏
      document.documentElement.requestFullscreen();
    } else {
      // 退出全屏
      if (document.exitFullscreen) {
        document.exitFullscreen();
      }
    }
    setTimeout(() => {
      windowInnerWidth.value = window.innerWidth;
      windowInnerHeight.value = window.innerHeight;
    }, 100);
  };
  const handleFullScreen = () => {
    screenWidth.value = SCREEN_SIZE[column.value].width;
    toggleFullScreen();
  };
  const grid = ref(null);
  const nodesArr = ref([]);
  onMounted(async () => {
    grid.value = GridStack.init({
      acceptWidgets: true,
      marginTop: 0,
      marginBottom: 16,
      marginLeft: 16,
      marginRight: 0,
      disableResize: true,
      float: true,
      row: 10,
      cellHeight: '108px'
    });
    nodesArr.value = props.nodes;
    await nextTick();
    grid.value.column(column.value);
    grid.value.disable(false); // 禁止拖动
    nodesArr.value.forEach(item => {
      grid.value.makeWidget(`#${item.id}`);
    });
    GridStack.setupDragIn('.sidebar .grid-stack-item', {
      appendTo: 'body',
      helper: 'clone'
    });
  });
  return {
    curMapId,
    column,
    screenTitle,
    decorationImgList,
    screenWidth,
    screenHeight,
    scaleSize,
    defaultTop,
    defaultLeft,
    getMinValue,
    handleFullScreen,
    nodesArr
  };
};
export default __sfc_main;
</script>

